<template>
  <v-row >
      <v-col cols="12" class="mt-5 mb-16 pa-10">
        <div class="d-flex">
        <div>
         <span class="heading primary--text font-weight-bold">Welcome</span><span class="subtitle secondary--text"> : Teller 1</span>
        </div>
        <v-spacer></v-spacer>
        <div>
         <span class="subtitle"> {{timer}} </span>
        </div>
        </div>
      </v-col>
      </v-row>
</template>

<script>
export default {
    name:"Timer",
    data() {
        return {
            timer: null,
            interval:null
            
        }
    },
   
    mounted() {
        this.interval = setInterval(() => {
             let timer = new Date().toLocaleTimeString()
            this.timer = timer
        }, 1000);
        
    },
    beforeDestroy() {
        clearTimeout(this.interval)
    },

}
</script>

